.submit-name {
  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    @include text-shadow(0.2rem, false);
  }
}

.name-group {
  display: flex;

  .name {
    font-family: gameboy;
    background: none;
    color: white;
    border: none;
    width: 3rem;
    margin: 0.3rem;    

    &:focus {
      outline: none;
    }
  }
}

